// Fonts
$fontCrimson: "Crimson";
$fontAhamono: "Ahamono";
$fontIcons: "APRIA-Icons";

$fontCrimsonStack: $fontCrimson,serif;
$fontAhamonoStack: $fontAhamono,monospace;
$fontIconsStack: $fontIcons;

@font-face{
	font-family: $fontCrimson;
	src: url('../webfonts/crimson-roman-webfont.woff') format('woff');
	font-style: normal;
	font-weight: normal;
}

@font-face{
    font-family: $fontCrimson;
    src: url('../webfonts/crimson-italic-webfont.woff') format('woff');
    font-style: italic;
    font-weight: normal;
}

@font-face{
	font-family: $fontCrimson;
	src: url('../webfonts/crimson-semibold-webfont.woff') format('woff');
	font-style: normal;
	font-weight: bold;
}

@font-face{
	font-family: $fontCrimson;
	src: url('../webfonts/crimson-semibolditalic-webfont.woff') format('woff');
	font-style: italic;
	font-weight: bold;
}

@font-face{
	font-family: $fontAhamono;
	src: url('../webfonts/AHAMONO-Regular.otf') format('opentype');
	font-style: normal;
	font-weight: normal;
}

@font-face{
	font-family: $fontIcons;
	src: url('../webfonts/icons.woff2') format('woff2'),
	     url('../webfonts/icons.woff') format('woff');
	font-style: normal;
	font-weight: normal;
}
